<template>
  <div class="play-bar" :class="{ show: !showPlayBar }" @mouseup="mouseup">
    <div
      @click="showPlayBar = !showPlayBar"
      class="item-up"
      :class="{ turn: !showPlayBar }"
    >
      <svg class="icon">
        <use xlink:href="#icon-jiantou-xia-cuxiantiao"></use>
      </svg>
    </div>
    <div class="kongjian">
      <!-- 上一首 -->
      <div class="item" @click="playPrev">
        <svg class="icon"><use xlink:href="#icon-ziyuanldpi"></use></svg>
      </div>
      <!-- 播放 -->
      <div class="item" @click="togglePlay">
        <svg class="icon"><use :xlink:href="playButtonUrl"></use></svg>
      </div>
      <!-- 下一首 -->
      <div class="item" @click="playNext">
        <svg class="icon"><use xlink:href="#icon-ziyuanldpi1"></use></svg>
      </div>
      <!-- 歌曲图片 -->
      <div class="item-img" @click="toLyric">
        <img :src="picUrl" />
      </div>

      <!-- 播放进度 -->
      <div class="playing-speed">
        <!-- 播放开始时间 -->
        <div class="current-time">{{ nowTime }}</div>
        <div class="progress-box">
          <!-- 歌曲名 -->
          <div class="item-song-title">
            <div>{{ title }}-{{ artist }}</div>
          </div>

          <div ref="progress" class="progress" @mousemove="mousemove">
            <!-- 进度条 -->
            <div ref="bg" class="bg" @click="updatemove">
              <div
                ref="curProgress"
                class="cur-progress"
                :style="{ width: curLength + '%' }"
              ></div>
            </div>
            <!-- 拖动的点 -->
            <div
              ref="idot"
              class="idot"
              :style="{ left: curLength + '%' }"
              @mousedown="mousedown"
            ></div>
          </div>
        </div>
        <!-- 歌曲总时长 -->
        <div class="left-time">{{ songTime }}</div>
        <!-- 音量调节 -->
        <div class="item item-volume" @onmouseover="changeVolume">
          <svg v-if="volume" class="icon">
            <use xlink:href="#icon-yinliang1"></use>
          </svg>
          <svg v-else class="icon">
            <use xlink:href="#icon-yinliangjingyinheix"></use>
          </svg>
          <el-slider
            class="volume"
            v-model="volume"
            :vertical="true"
          ></el-slider>
        </div>
        <!-- 收藏歌曲 -->
        <div class="item" @click="collectSong">
          <svg
            :class="{ active: isLoggedIn && isCollectedActive }"
            class="icon"
          >
            <use xlink:href="#icon-xihuan-shi"></use>
          </svg>
        </div>
        <!-- 下载 -->
        <div class="item" @click="downloadSongs">
          <svg class="icon"><use xlink:href="#icon-xiazai"></use></svg>
        </div>
        <!-- 当前播放歌曲列表 -->
        <div class="item" @click="clickPlayList">
          <svg class="icon"><use xlink:href="#icon-liebiao"></use></svg>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mixin } from "../mixins";
import { mapGetters } from "vuex";
import { download, addCollection, collectionOfUserId } from "../api/index";
export default {
  name: "play-bar",
  mixins: [mixin],
  data() {
    return {
      nowTime: "00:00",
      songTime: "00:00",
      curLength: 0,
      progressLength: 0,
      mouseStartX: 0,
      tag: false,
      volume: 50,
      showPlayBar: true, //显示或隐藏播放器
      collectionLists: [] //当前用户收藏的歌曲
    };
  },
  computed: {
    ...mapGetters([
      "id",
      "url",
      "isPlay",
      "playButtonUrl",
      "picUrl",
      "title",
      "artist",
      "lyric",
      "duration",
      "curTime",
      "showPlayList",
      "listIndex",
      "listOfSongs",
      "autoNext", //用于自动触发播放下一首
      "isLoggedIn", //用户是否登录
      "userId", //当前用户登录id
      "isCollected" //是否已收藏
    ]),
    isCollectedActive() {
      for (let item of this.collectionLists) {
        if (item.songId == this.id) {
          return true;
        }
      }
      return false;
    }
  },
  watch: {
    isPlay() {
      if (this.isPlay) {
        this.$store.commit("setPlayButtonUrl", "#icon-zanting");
      } else {
        this.$store.commit("setPlayButtonUrl", "#icon-bofang");
      }
    },
    curTime() {
      this.nowTime = this.formatSeconds(this.curTime);
      this.songTime = this.formatSeconds(this.duration);
    },
    // 监控音量变化
    volume() {
      this.$store.commit("setVolume", this.volume / 100);
    },
    // 自动播放下一首
    autoNext() {
      this.playNext();
    }
  },
  created() {
    collectionOfUserId(this.userId).then(res => {
      this.collectionLists = res;
    });
  },
  mounted() {
    this.progressLength = this.$refs.progress.getBoundingClientRect().width;
    // 点击音量图标显示音量条
    document.querySelector(".item-volume").addEventListener(
      "click",
      function(e) {
        document.querySelector(".volume").classList.add("show-volume");
        e.stopPropagation();
      },
      false
    );
    // 阻止对父控件进行操作
    document.querySelector(".volume").addEventListener(
      "click",
      function(e) {
        e.stopPropagation();
      },
      false
    );
    // 点击音量图标隐藏音量条
    document.addEventListener(
      "click",
      function() {
        document.querySelector(".volume").classList.remove("show-volume");
      },
      false
    );
  },
  methods: {
    // 播放按钮
    togglePlay() {
      if (this.isPlay) {
        this.$store.commit("setIsPlay", false);
      } else {
        this.$store.commit("setIsPlay", true);
      }
    },
    // 解析时间
    formatSeconds(value) {
      let theTime = parseInt(value);
      let result = ""; //返回值
      // 小时
      let hour = parseInt(theTime / 3600);
      if (hour > 0) {
        if (hour < 10) {
          result = "0" + hour + ":";
        } else {
          result = hour;
        }
      }
      // 分钟
      let minute = parseInt((theTime / 60) % 60);
      if (minute == 0) {
        result = "00" + ":";
      } else if (minute > 0) {
        if (minute < 10) {
          result += "0" + minute + ":";
        } else {
          result += minute + ":";
        }
      }
      // 秒数
      let second = parseInt(theTime % 60);
      if (second == 0) {
        result += "00";
      }
      if (second > 0) {
        if (second < 10) {
          result += "0" + second;
        } else {
          result += second;
        }
      }
      return result;
    },
    // 按下鼠标开始拖拽
    mousedown(e) {
      this.mouseStartX = e.clientX;
      this.tag = true;
    },
    // 拖拽完成松开鼠标
    mouseup() {
      this.tag = false;
    },
    // 拖拽过程中
    mousemove(e) {
      if (!this.duration) {
        return false;
      }
      if (this.tag) {
        let movementX = e.clientX - this.mouseStartX;
        let currentLength = this.$refs.curProgress.getBoundingClientRect()
          .width;
        let newPercent =
          ((movementX + currentLength) / this.progressLength) * 100;
        if (newPercent > 100) {
          newPercent = 100;
        }
        this.curLength = newPercent;
        this.mouseStartX = e.clientX;
        this.changeTime(newPercent);
      }
    },
    //更改歌曲进度
    changeTime(percent) {
      let newCurTime = percent * 0.01 * this.duration;
      this.$store.commit("setChangeTime", newCurTime);
    },
    // 点击进度条切换播放进度
    updatemove(e) {
      if (!this.tag) {
        //进度条的左侧坐标
        let curLength = this.$refs.bg.offsetLeft;
        let newPercent = ((e.clientX - curLength) / this.progressLength) * 100;
        if (newPercent > 100) {
          newPercent = 100;
        } else if (newPercent < 0) {
          newPercent = 0;
        }
        this.curLength = newPercent;
        this.changeTime(newPercent);
      }
    },
    //调节音量
    changeVolume() {
      document.querySelector(".volume").classList.add("show-volume");
    },
    //显示播放列表
    clickPlayList() {
      this.$store.commit("setPlayList", true);
    },
    //播放上一首
    playPrev() {
      // console.log(this.listIndex);
      // 列表有歌曲且不只一首
      if (this.listIndex != -1 && this.listOfSongs.length > 1) {
        // 如果当前播放的不是第一首，则直接返回上一首
        if (this.listIndex > 0) {
          this.$store.commit("setListIndex", this.listIndex - 1);
        } else {
          // 播放的是第一首，则切到列表最后一首
          this.$store.commit("setListIndex", this.listOfSongs.length - 1);
        }
        this.toPlay(
          this.listOfSongs[this.listIndex].id,
          this.listOfSongs[this.listIndex].url,
          this.listOfSongs[this.listIndex].pic,
          this.listIndex,
          this.listOfSongs[this.listIndex].name,
          this.parsecLyric(this.listOfSongs[this.listIndex].lyric)
        );
      }
    },
    //播放下一首
    playNext() {
      // console.log(this.listIndex);
      // 列表有歌曲且不只一首
      if (this.listIndex != -1 && this.listOfSongs.length > 1) {
        // 如果当前播放的不是最后一首，则直接到下一首
        if (this.listIndex < this.listOfSongs.length - 1) {
          this.$store.commit("setListIndex", this.listIndex + 1);
        } else {
          // 播放的是最后一首，则切到列表第一首
          this.$store.commit("setListIndex", 0);
        }
        this.toPlay(
          this.listOfSongs[this.listIndex].id,
          this.listOfSongs[this.listIndex].url,
          this.listOfSongs[this.listIndex].pic,
          this.listIndex,
          this.listOfSongs[this.listIndex].name,
          this.parsecLyric(this.listOfSongs[this.listIndex].lyric)
        );
      }
    },
    // 转向歌词页面
    toLyric() {
      this.$router.push({ path: `/lyric` });
    },
    // 下载音乐
    downloadSongs() {
      download(this.url)
        .then(res => {
          let content = res.data;
          let eleLink = document.createElement("a");
          eleLink.download = `${this.artist}--${this.title}.mp3`;
          eleLink.style.display = "none";
          // 把字符内容转换成blob地址
          let blob = new Blob([content]);
          eleLink.href = URL.createObjectURL(blob);
          // 把链接地址加到document里
          document.body.appendChild(eleLink);
          // 触发点击
          eleLink.click();
          // 然后移除这个新加的控件
          document.body.removeChild(eleLink);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 收藏歌曲
    collectSong() {
      if (this.isLoggedIn) {
        var params = new URLSearchParams();
        params.append("userId", this.userId);
        params.append("type", 0);
        params.append("songId", this.id);
        addCollection(params).then(res => {
          if (res.code == 1) {
            collectionOfUserId(this.userId).then(res => {
              this.collectionLists = res;
            });
            this.notify("收藏成功", "success");
          } else if (res.code == 2) {
            this.notify("已收藏", "warning");
          } else {
            this.notify("收藏失败", "error");
          }
        });
      } else {
        this.notify("请先登录", "warning");
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/play-bar.scss";
</style>



