import { likeSongOfName } from "../api";

export const mixin = {
  methods: {
    // 提示信息
    notify(title, type) {
      this.$notify({
        title: title,
        type: type
      });
    },
    // 获取图片地址
    attachImageUrl(srcUrl) {
      return srcUrl
        ? this.$store.state.configure.HOST + srcUrl
        : this.$store.state.configure.HOST + "/img/user.jpg";
    },
    // 根据歌手名字模糊查询歌曲
    getSong() {
      if (!this.$route.query.keyWords) {
        this.$store.commit("setListOfSongs", []);
        this.notify("您输入的内容为空", "warning");
      } else {
        likeSongOfName(this.$route.query.keyWords).then(res => {
          if (!res.length) {
            this.$store.commit("setListOfSongs", []);
            this.notify("系统暂无符合条件的歌曲", "warning");
          } else {
            console.log(res);
            this.$store.commit("setListOfSongs", res);
            this.$store.commit("setListIndex", 0);
          }
        });
      }
    },
    // 获取名字前半部分（歌手名）
    replaceFName(str) {
      let arr = str.split("-");
      return arr[0];
    },
    // 获取名字后半部分（歌曲名）
    replaceLName(str) {
      let arr = str.split("-");
      return arr[1];
    },
    // 播放
    toPlay(id, url, pic, index, name, lyric) {
      this.$store.commit("setId", id);
      this.$store.commit("setUrl", this.$store.state.configure.HOST + url);
      this.$store.commit("setPicUrl", this.$store.state.configure.HOST + pic);
      this.$store.commit("setListIndex", index);
      this.$store.commit("setTitle", this.replaceLName(name));
      this.$store.commit("setArtist", this.replaceFName(name));
      this.$store.commit("setLyric", this.parsecLyric(lyric));
    },
    // 解析歌词
    parsecLyric(text) {
      let lines = text.split("\n"); //将歌词按行分解成数组
      let pattern = /\[\d{2}:\d{2}.(\d{3}|\d{2})\]/g; //时间格式的正则表达式
      let results = []; //返回值
      // 歌词格式不正确直接返回
      if (!/\[.+\]/.test(text)) return [[0, text]];

      // 去掉前面格式不正确的行
      while (!pattern.test(lines[0])) {
        lines = lines.slice(1);
      }
      // 遍历每一行，形成一个每行有两个元素的数组
      for (let line of lines) {
        let time = line.match(pattern); //第一个元素是以秒为计算单位的时间
        let lyric = line.replace(pattern, ""); //第二个元素是歌词
        for (let item of time) {
          let t = item.slice(1, -1).split(":");
          if (lyric != "") {
            results.push([parseInt(t[0], 10) * 60 + parseFloat(t[1]), lyric]);
          }
        }
      }
      //按照第一个元素--时间--排序
      results.sort(function(a, b) {
        return a[0] - b[0];
      });

      return results;
    },
    //显示生日
    showBirth(val) {
      return val.substr(0, 10);
    }
  }
};



