<template>
  <div class="info">
    <div class="title"><span>编辑个人信息</span></div>
    <hr />
    <div class="personal">
      <el-form
        :model="registerForm"
        ref="registerForm"
        label-width="70px"
        class="demo-ruleForm"
        :rule="rules"
      >
        <el-form-item prop="username" label="用户名">
          <el-input v-model="registerForm.username" placeholder="用户名"
            >用户名</el-input
          >
        </el-form-item>
        <el-form-item prop="password" label="密码">
          <el-input
            type="password"
            v-model="registerForm.password"
            placeholder="密码"
            >密码</el-input
          >
        </el-form-item>
        <el-form-item prop="sex" label="性别">
          <el-radio-group v-model="registerForm.sex">
            <el-radio :label="0">女</el-radio>
            <el-radio :label="1">男</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item prop="phoneNumber" label="手机号码">
          <el-input
            v-model="registerForm.phoneNumber"
            placeholder="手机号码"
          ></el-input>
        </el-form-item>
        <el-form-item prop="email" label="邮箱">
          <el-input v-model="registerForm.email" placeholder="邮箱"></el-input>
        </el-form-item>
        <el-form-item prop="birthday" label="生日">
          <el-date-picker
            v-model="registerForm.birthday"
            size="normal"
            placeholder="选择日期时间"
            style="width: 100%;"
            :editable="false"
            type="date"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item prop="introduction" label="签名">
          <el-input
            v-model="registerForm.introduction"
            placeholder="签名"
          ></el-input>
        </el-form-item>
        <el-form-item prop="location" label="地区">
          <el-select
            v-model="registerForm.location"
            value-key="index"
            placeholder="选择地区"
            clearable
            filterable
            style="width: 100%;"
          >
            <el-option
              v-for="city in cities"
              :key="city.value"
              :label="city.label"
              :value="city.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <div class="login-btn">
          <el-button type="primary" @click="submit">保存</el-button>
          <el-button type="danger" @click="goback(-1)">取消</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { cities, rules } from "../../assets/data/form";
import { mixin } from "../../mixins/index";
import { getUserById, updateUser } from "../../api/index";

export default {
  name: "user-info",
  mixins: [mixin],
  data() {
    return {
      registerForm: {
        username: "", //用户名
        password: "", //密码
        sex: "", //性别
        phoneNumber: "", //手机号码
        email: "", //邮箱
        birthday: "", //生日
        introduction: "", //签名
        location: "" //地区
      },
      cities: [], //地区选择
      rules: [] //匹配规则
    };
  },
  methods: {
    getMsg(userId) {
      getUserById(userId).then(res => {
        this.registerForm.username = res.username;
        this.registerForm.password = res.password;
        this.registerForm.sex = res.sex;
        this.registerForm.phoneNumber = res.phoneNum;
        this.registerForm.email = res.email;
        this.registerForm.birthday = res.birth;
        this.registerForm.introduction = res.introduction;
        this.registerForm.location = res.location;
      }); //#TODO:
    },
    submit() {
      let d = new Date(this.registerForm.birthday);
      let dateTime =
        d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
      let params = new URLSearchParams();
      params.append("id", this.userId);
      params.append("username", this.registerForm.username);
      params.append("password", this.registerForm.password);
      params.append("sex", this.registerForm.sex);
      params.append("phoneNum", this.registerForm.phoneNumber);
      params.append("email", this.registerForm.email);
      params.append("birth", dateTime);
      params.append("introduction", this.registerForm.introduction);
      params.append("location", this.registerForm.location);
      params.append("avator", "/avatorImages/defaultUserHeader.jpg");
      updateUser(params)
        .then(res => {
          if (res.code === 1) {
            this.notify("保存成功!正在前往登录...", "success");
            setTimeout(() => this.$router.push("login"), 3000);
            this.isLoggedIn = false;
          } else {
            this.notify("保存失败", "error");
          }
        })
        .catch(err => this.notify("保存失败", err));
    },
    goback(index) {
      this.$router.go(index);
    }
  },
  computed: {
    ...mapGetters([
      "userId", //当前用户id
      "isLoggedIn" //用户是否登录
    ])
  },
  created() {
    this.cities = cities;
    this.rules = rules;
    this.getMsg(this.userId);
  }
};
</script>
<style lang="scss" scoped>
@import "../../assets/css/info.scss.";
</style>



