<template>
  <div class="my-music">
    <div class="album-slide">
      <div class="album-img"><img :src="attachImageUrl(avator)" /></div>
      <ul class="album-info">
        <li>昵称：{{ userName }}</li>
        <li>性别：{{ userSex }}</li>
        <li>生日：{{ birth }}</li>
        <li>地区：{{ location }}</li>
      </ul>
    </div>
    <div class="album-content">
      <div class="album-title">个性签名：{{ introduction }}</div>
      <div class="songs-body">
        <album-content :songList="collectLists"
          ><template slot="title">我的收藏</template></album-content
        >
      </div>
    </div>
  </div>
</template>
<script>
import { mixin } from "../mixins";
import { mapGetters } from "vuex";
import { getUserById, collectionOfUserId, songOfSongId } from "../api/index";
import AlbumContent from "../components/search/AlbumContent";
export default {
  name: "my-music",
  mixins: [mixin],
  data() {
    return {
      avator: "", //用户头像
      userName: "", //用户名
      userSex: "", //用户性别
      birth: "", //用户生日
      location: "", //用户地区
      introduction: "", //个性签名
      collections: [], //收藏的歌曲列表
      collectLists: [] //收藏的歌曲列表（带歌曲详情）
    };
  },

  components: {
    AlbumContent
  },
  methods: {
    // 获取用户信息
    getMsg() {
      getUserById(this.userId).then(res => {
        this.avator = res.avator;
        this.userName = res.username;
        if (res.sex == 0) {
          this.userSex = "女";
        } else if (res.sex == 1) {
          this.userSex = "userS";
        } else {
          this.userSex = "其他";
        }
        this.birth = this.showBirth(res.birth);
        this.location = res.location;
        this.introduction = res.introduction;
      });
    },
    // 获取收藏列表
    getCollection() {
      collectionOfUserId(this.userId)
        .then(res => {
          this.collections = res;
          this.getSongDetail();
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 获取歌曲详情
    getSongDetail() {
      for (let item of this.collections) {
        songOfSongId(item.songId).then(res => {
          this.collectLists.push(res);
        });
      }
    }
  },
  computed: {
    ...mapGetters([
      "listOfSongs", //当前播放列表
      "userId" //当前登录用户id
    ])
  },
  mounted() {
    this.getMsg();
    this.getCollection();
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/my-music.scss";
</style>



