<template
  ><div class="search-song-Lists">
    <content-list :contentList="searchResults"></content-list></div
></template>
<script>
import { getSongListOfLikeTitle } from "../../api/index";
import ContentList from "../ContentList.vue";
import { mixin } from "../../mixins";
export default {
  name: "search-song-lists",
  mixins: [mixin],
  components: {
    ContentList
  },
  data() {
    return {
      searchResults: []
    };
  },
  methods: {
    getSearchList() {
      if (!this.$route.query.keyWords) {
        this.notify("您输入的内容为空", "warning");
      } else {
        getSongListOfLikeTitle(this.$route.query.keyWords)
          .then(res => {
            console.log("执行搜索");
            console.log(res);
            if (res) {
              this.searchResults = res;
            } else {
              this.notify("暂无该歌单", "warning");
            }
          })
          .catch(err => console.log(err));
      }
    }
  },
  mounted() {
    this.getSearchList();
  }
};
</script>
<style>
@import "../../assets/css/search-song-Lists.scss";
</style>



