<template>
  <div>
    <loginLogo></loginLogo>
    <div class="signUp">
      <div class="signUp-head"><span>用户登录</span></div>
      <el-form
        :model="loginForm"
        ref="loginForm"
        label-width="70px"
        class="demo-ruleForm"
      >
        <el-form-item prop="username" label="用户名">
          <el-input v-model="loginForm.username" placeholder="用户名"
            >用户名</el-input
          >
        </el-form-item>
        <el-form-item prop="password" label="密码">
          <el-input
            type="password"
            v-model="loginForm.password"
            placeholder="密码"
            >密码</el-input
          >
        </el-form-item>
        <div class="login-btn">
          <el-button type="primary" @click="submit">登录</el-button>
          <el-button type="danger" @click="goSignUp">注册</el-button>
        </div>
      </el-form>
    </div>
  </div></template
>
<script>
import { mixin } from "../mixins/index";
import { login } from "../api/index";
import loginLogo from "../pages/LoginLogo.vue";

export default {
  mixins: [mixin],
  data() {
    return {
      loginForm: {
        username: "", //用户名
        password: "" //密码
      },
      rules: {
        username: [
          { required: true, trigger: "blur", message: "请输入用户名" }
        ],
        password: [{ required: true, trigger: "blur", message: "请输入密码" }]
      }
    };
  },
  methods: {
    submit() {
      let params = new URLSearchParams();
      params.append("username", this.loginForm.username);
      params.append("password", this.loginForm.password);

      login(params)
        .then(res => {
          if (res.code === 1) {
            this.notify("登录成功!", "success");
            this.$store.commit("setIsLoggedIn", true);
            this.$store.commit("setUserId", res.userMsg.id);
            this.$store.commit("setUsername", res.userMsg.username);
            this.$store.commit("setAvator", res.userMsg.avator);
            this.changeActiveName("首页");
            setTimeout(() => this.$router.push("/"), 500);
          } else {
            this.notify("用户名或密码错误", "error");
          }
        })
        .catch(err => this.notify("登录失败", err));
    },
    goSignUp() {
      this.$router.push("/sign-up");
      this.changeActiveName("注册");
    },
    changeActiveName(activeName) {
      this.$store.commit("setActiveName", activeName);
    }
  },
  mounted() {
    this.changeActiveName("首页");
  },
  components: {
    loginLogo
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/sign-up.scss.";
</style>



