<template>
  <div class="comment">
    <h2>评论</h2>
    <div class="comment-msg">
      <div class="comment-img"><img :src="attachImageUrl(avator)" /></div>
      <el-input
        class="comment-input"
        type="textarea"
        v-model="commentText"
        placeholder="评论一下..."
      ></el-input>
    </div>
    <el-button type="primary" size="mini" @click="postComment">发表</el-button>
    <div>
      <br />
      <p>共{{ commentList.length }}条评论</p>
      <hr />
      <ul class="popular" v-for="(item, index) in commentList" :key="index">
        <li>
          <div class="popular-img">
            <img :src="attachImageUrl(userAvators[index])" />
          </div>
          <div class="popular-msg">
            <ul>
              <li class="name">
                {{ userNames[index] }}
              </li>
              <li class="time">{{ item.createTime }}</li>
              <li class="content">{{ item.content }}</li>
              <!-- <li class="content">{{ item }}</li> -->
            </ul>
          </div>
          <div class="up" ref="up" @click="postUp(item.id, item.up, index)">
            <svg class="icon"><use xlink:href="#icon-zan"></use></svg>
            {{ item.up }}
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { mixin } from "../mixins";
import {
  getAllComments,
  addComment,
  getUserById,
  postLike
} from "../api/index";

import { mapGetters } from "vuex";

export default {
  name: "comment",
  mixins: [mixin],
  props: [
    "playId",
    "type" //0歌曲，1歌单
  ],
  computed: {
    ...mapGetters([
      "id",
      "isLoggedIn",
      "userId",
      "avator" //当前登录用户头像
    ])
  },
  data() {
    return {
      commentText: "",
      commentList: [],
      userAvators: [],
      userNames: [] //评论的用户们的名称
    };
  },
  methods: {
    // 提交评论
    postComment() {
      if (this.isLoggedIn) {
        let params = new URLSearchParams();
        if (this.type === 0) {
          //评论歌曲
          params.append("songId", this.playId);
        } else {
          //评论歌单
          params.append("songListId", this.playId);
        }
        params.append("userId", this.userId);
        params.append("type", this.type);
        params.append("content", this.commentText);
        addComment(params)
          .then(res => {
            if (res.code === 1) {
              this.notify("评论成功！", "success");
              this.commentText = "";
              this.getComment();
            } else {
              this.notify("评论失败！", "error");
            }
          })
          .catch(err => {
            this.notify("评论失败！", "error");
            console.log(err);
          });
      } else {
        this.notify("请先登录", "warning");
      }
    },
    // 获取评论列表
    getComment() {
      // console.log(this.type, this.playId);
      getAllComments(this.type, this.playId)
        .then(res => {
          this.commentList = res;
          for (let item of res) {
            this.getCommentUser(item.userId);
          }
        })
        .catch(err => {
          this.notify("评论信息加载失败！", "error");
          console.log(err);
        });
    },
    // 获取评论了的用户信息
    getCommentUser(id) {
      getUserById(id)
        .then(res => {
          this.userAvators.push(res.avator);
          this.userNames.push(res.username);
        })
        .catch(err => {
          this.notify("用户列表获取失败！", "error");
          console.log(err);
        });
    },
    // 给评论点赞
    postUp(id, up, index) {
      if (this.isLoggedIn) {
        let params = new URLSearchParams();
        params.append("id", id);
        params.append("up", up + 1);
        postLike(params)
          .then(res => {
            if (res.code === 1) {
              this.$refs.up[index].children[0].style.color = "#2796cd";
              this.notify("点赞成功！", "success");
              this.getComment();
            } else {
              this.notify("点赞失败！", "error");
            }
          })
          .catch(err => {
            this.notify("点赞失败！", "error");
            console.log(err);
          });
      } else {
        this.notify("请先登录", "warning");
      }
    }
  },
  mounted() {
    this.getComment();
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/comment.scss";
</style>



