<!-- 主页展示歌单或歌手 -->
<template>
  <div class="section-list">
    <ul class="section-content">
      <li
        class="content-item"
        v-for="(item, index) in contentList"
        :key="index"
        @click="goAlbum(item, item.name)"
      >
        <div class="kuo">
          <img class="item-img" :src="attachImageUrl(item.pic)" />
          <div class="mask">
            <svg class="icon"><use xlink:href="#icon-bofang"></use></svg>
          </div>
        </div>
        <p class="item-name">{{ item.name || item.title }}</p>
      </li>
    </ul>
  </div>
</template>
<script>
import { mixin } from "../mixins";
export default {
  name: "content-list",
  props: ["contentList"],
  mixins: [mixin],
  methods: {
    goAlbum(item, type) {
      this.$store.commit("setTempList", item);
      if (type) {
        this.$router.push(`/singer-album/${item.id}`); //歌手
      } else {
        this.$router.push(`/song-list-album/${item.id}`); //歌单
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/content-list.scss";
</style>



