<template>
  <div class="singer-album">
    <div class="album-slide">
      <div class="singer-img"><img :src="attachImageUrl(singer.pic)" /></div>
      <ul class="info">
        <li v-if="singer.sex || singer.sex == 0">
          性别：{{ showSex(singer.sex) }}
        </li>
        <li>生日：{{ showBirth(singer.birth) }}</li>
        <li>地区：{{ singer.location }}</li>
      </ul>
    </div>
    <div class="album-content">
      <div class="intro">
        <h2>{{ singer.name }}</h2>
        <span>{{ singer.introduction }}</span>
      </div>
    </div>
    <div class="content">
      <album-content :songList="listOfSongs">
        <template slot="title"
          >歌单
          <hr
        /></template>
      </album-content>
      <!-- <comment :playId="songListId" :type="1"></comment> -->
    </div>
  </div>
</template>
<script>
import { mixin } from "../mixins";
import { mapGetters } from "vuex";
import { songOfSingerId } from "../api/index";

import AlbumContent from "../components/search/AlbumContent.vue";
export default {
  name: "singer-album",
  mixins: [mixin],
  components: {
    AlbumContent
  },
  data() {
    return {
      singerId: "", //前面传来的歌手id
      singer: {} //当前歌手信息
    };
  },
  computed: {
    ...mapGetters([
      "listOfSongs", //当前播放列表
      "tempList", //当前歌手对象
      "isLoggedIn", //用户是否登录
      "userId" //用户id
    ])
  },
  created() {
    this.singerId = this.$route.params.id;
    this.singer = this.tempList;
    this.getSongBySingerId();
  },
  methods: {
    // 根据歌手id查询歌曲
    getSongBySingerId() {
      songOfSingerId(this.singerId)
        .then(res => {
          this.$store.commit("setListOfSongs", res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 展示性别
    showSex(sex) {
      if (sex === 0) {
        return "女";
      } else if (sex === 1) {
        return "男";
      } else {
        return "其他";
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/singer-album.scss";
</style>



