import { get, post } from "./http";
import Axios from "axios";

//==================歌手相关==================
// 查询歌手
export const getAllSinger = () => get(`singer/allSinger`);

//==================歌曲相关==================
// 根据歌手id查询歌曲
export const songOfSingerId = id => get(`song/singer/detail?singerId=${id}`);

//根据歌曲id查询歌曲对象
export const songOfSongId = id => get(`song/detail?songId=${id}`);

// 根据歌曲名字模糊查询歌曲
export const likeSongOfName = keywords =>
  get(`song/likeSongOfName?songName=${keywords}`);
//==================歌单相关==================
// 查询歌单
export const getAllSongList = () => get(`songList/allSongList`);
//根据歌单标题模糊查询列表
export const getSongListOfLikeTitle = keywords =>
  get(`songList/likeTitle?title=${keywords}`);
//根据歌单风格模糊查询列表
export const getSongListLikeStyle = style =>
  get(`songList/likeStyle?style=${style}`);
//==================歌单的歌曲相关==================
// 根据歌单id查询歌曲列表
export const listSongDetail = songListId =>
  get(`listSong/detail?songListId=${songListId}`);

//==================用户相关==================
// 查询用户
export const getAllConsumer = () => get(`consumer/allConsumer`);
//用户注册
export const signUp = params => post(`consumer/add`, params);
//用户登录
export const login = params => post(`consumer/login`, params);
// 根据用户id查询该用户的详细信息
export const getUserById = id => get(`/consumer/selectByPrimaryKey?id=${id}`);
//修改用户信息
export const updateUser = params => post(`consumer/update`, params);
// 下载音乐
export const download = url =>
  Axios({
    method: "get",
    url: url,
    responseType: "blob"
  });

//==================评分相关==================
// 提交评分
export const submitRank = params => post(`/rank/add`, params);
// 获取指定歌单的平均分
export const avgRank = songListId => get(`/rank/avg?songListId=${songListId}`);

//==================评论相关==================
// 提交评论
export const addComment = params => post(`/comment/add`, params);
// 给某个评论点赞
export const postLike = params => post(`/comment/like`, params);

// 修改评论
export const updateComment = params => post(`/comment/update`, params);
// 删除评论
export const deleteComment = id => get(`/comment/delete?id=${id}`);
// 返回当前歌单或歌曲的评论列表
export const getAllComments = (type, id) => {
  if (type === 0) {
    //歌曲
    return get(`/comment/commentOfSong?songId=${id}`);
  } else {
    //歌单
    return get(`/comment/commentOfSongList?songListId=${id}`);
  }
};

