<template>
  <div class="singer">
    <ul class="singer-header">
      <li
        v-for="(item, index) in singerLocation"
        :key="index"
        @click="showSingerByLocation(item.name)"
        :class="{ active: item.name == activeName }"
      >
        {{ item.name }}
      </li>
    </ul>
    <div>
      <content-list :contentList="tableData"></content-list>
      <div class="pagination">
        <el-pagination
          @current-change="getCurrentPage"
          :current-page.sync="currentPage"
          :page-sizes="[20, 40, 80, 100]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="albumDatas.length"
          background
        >
          :pager-count="7">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import ContentList from "../components/ContentList.vue";
import { getAllSinger } from "../api/index";
import { mixin } from "../mixins";
import { singerLocation } from "../assets/data/singerLocation";
export default {
  name: "singer",
  mixins: [mixin],
  components: {
    ContentList
  },
  data() {
    return {
      albumDatas: [], //歌手数据
      pageSize: 15, //页面大小，一页十五条数据
      currentPage: 1, //当前页
      singerLocation: [], //歌手风格
      activeName: "全部歌手" //选中的风格
    };
  },
  computed: {
    //计算当前表格中的数据
    tableData() {
      return this.albumDatas.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    }
  },
  methods: {
    // 获取全部歌手
    getSinger() {
      getAllSinger().then(res => (this.albumDatas = res));
      this.currentPage = 1;
    },
    //获取当前页
    getCurrentPage(val) {
      this.currentPage = val;
    },
    // 根据风格查询歌手
    getSingerByLocation(style) {
      getSongListLikeStyle(style).then(res => (this.albumDatas = res));
      this.currentPage = 1;
    },
    // 根据风格显示歌手
    showSingerByLocation(name) {
      this.activeName = name;
      this.albumDatas = [];
      if (name == "全部歌手") {
        this.getSinger();
      } else {
        this.getSingerByLocation(name);
      }
    }
  },
  mounted() {
    this.getSinger();
    this.singerLocation = singerLocation;
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/singer.scss";
</style>



