<template
  ><div class="setting">
    <div class="leftCol">
      <div class="settingMainHeader">设置</div>
      <ul class="setting-aside">
        <li
          v-for="(item, index) in settingList"
          :key="index"
          :class="{ activeColor: activeName == item.name }"
          @click="handleClick(item)"
        >
          {{ item.name }}
        </li>
      </ul>
    </div>
    <div class="contentCol">
      <component :is="componentSrc"></component>
    </div></div
></template>
<script>
import { mixin } from "../mixins";
import UserInfo from "../components/userSettings/UserInfo.vue";
import AvatorSetting from "../components/userSettings/AvatorSetting.vue";
export default {
  name: "user-setting",
  mixins: [mixin],
  data() {
    return {
      settingList: [
        { name: "个人信息", path: "UserInfo" },
        { name: "修改头像", path: "AvatorSetting" }
      ],
      activeName: "个人信息",
      componentSrc: "UserInfo"
    };
  },
  components: {
    UserInfo,
    AvatorSetting
  },
  methods: {
    handleClick(item) {
      this.activeName = item.name;
      this.componentSrc = item.path;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/setting.scss";
</style>



