<template>
  <div class="song-list-album">
    <div class="album-slide">
      <div class="album-img"><img :src="attachImageUrl(tempList.pic)" /></div>
      <div class="album-info">
        <h2>简介：</h2>
        <span>{{ tempList.introduction }}</span>
      </div>
    </div>
    <div class="album-content">
      <div class="album-title">
        <p>{{ tempList.title }}</p>
      </div>
      <div class="album-score">
        <div>
          <h3>歌单评分：</h3>
          <div><el-rate v-model="average" disabled></el-rate></div>
        </div>
        <span>{{ average * 2 }}</span>
        <div>
          <h3>我的评分</h3>
          <div @click="setRank">
            <el-rate v-model="rank" allow-half show-text></el-rate>
          </div>
        </div>
      </div>

      <div class="songs-body">
        <album-content :songList="listOfSongs">
          <template slot="title"
            >歌单
            <hr
          /></template>
        </album-content>
        <comment :playId="songListId" :type="1"></comment>
      </div>
    </div>
  </div>
</template>
<script>
import { mixin } from "../mixins";
import { mapGetters } from "vuex";
import {
  listSongDetail,
  songOfSongId,
  submitRank,
  avgRank
} from "../api/index";

import AlbumContent from "../components/search/AlbumContent.vue";
import Comment from "../components/Comment.vue";

export default {
  name: "song-list-album",
  mixins: [mixin],
  components: {
    AlbumContent,
    Comment
  },
  data() {
    return {
      songs: [], //当前页面需要展示的歌曲列表
      songListId: "", //前面传来的歌单id
      average: 0, //平均分
      rank: 0 //提交的评分
    };
  },
  methods: {
    // 获取当前歌单的歌曲列表
    getSongs() {
      listSongDetail(this.songListId)
        .then(res => {
          for (let item of res) {
            this.getSongsDetail(item.songId);
          }
          this.$store.commit("setListOfSongs", this.songs);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 根据歌曲id获取歌曲信息
    getSongsDetail(id) {
      songOfSongId(id)
        .then(res => {
          this.songs.push(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    //获取歌单评分
    getAvgRank(id) {
      avgRank(id)
        .then(res => {
          this.average = res;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //给歌单打分
    setRank() {
      if (this.isLoggedIn) {
        let params = new URLSearchParams();
        params.append("songListId", this.songListId);
        params.append("consumerId", this.userId);
        params.append("score", this.rank * 2);

        submitRank(params)
          .then(res => {
            if (res.code === 1) {
              this.notify("评分成功！", "success");
              this.getAvgRank(this.songListId);
            } else {
              this.notify("评分失败！", "error");
            }
          })
          .catch(err => {
            this.notify("你已经评价过了", "error");
            console.log(err);
          });
      } else {
        this.rank = null;
        this.notify("请先登录", "waring");
      }
    }
  },
  created() {
    this.songListId = this.$route.params.id;
    this.getSongs();
    this.getAvgRank(this.songListId);
  },
  computed: {
    ...mapGetters([
      "listOfSongs", //当前播放列表
      "tempList", //当前歌单对象
      "isLoggedIn", //用户是否登录
      "userId" //用户id
    ])
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/song-list-album.scss";
</style>



