<template>
  <div class="search" style="margin-top: 150px;">
    <nav class="searchList-nav" ref="change">
      <span
        :class="{ isActive: toggle == 'Songs' }"
        @click="handleChangeView('Songs')"
        >歌曲</span
      >
      <span
        :class="{ isActive: toggle == 'SongLists' }"
        @click="handleChangeView('SongLists')"
        >歌单</span
      >
    </nav>
    <component :is="currentView"></component>
  </div>
</template>
<script>
import SearchSongLists from "../components/search/SearchSongLists.vue";
import SearchSongs from "../components/search/SearchSongs.vue";

export default {
  name: "search",
  components: { SearchSongLists, SearchSongs },
  data() {
    return {
      toggle: "Songs",
      currentView: "searchSongs"
    };
  },
  methods: {
    // 切换组件
    handleChangeView(component) {
      this.currentView = "search" + component;
      this.toggle = component;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/search.scss";
</style>



