const configure = {
  state: {
    HOST: "http://127.0.0.1:8888", // 后台访问地址根目录
    activeName: "", //当前选中的菜单名
    showPlayList: false, //是否显示播放列表
    isLoggedIn: false, //用户是否登录
    isCollected: false //用户是否已收藏
  },
  getters: {
    activeName: state => {
      let activeName = state.activeName;
      if (!activeName) {
        activeName = JSON.parse(window.sessionStorage.getItem("activeName"));
      }
      return activeName;
    },
    showPlayList: state => {
      let showPlayList = state.showPlayList;
      if (!showPlayList) {
        showPlayList = JSON.parse(
          window.sessionStorage.getItem("showPlayList")
        );
      }
      return showPlayList;
    },
    isLoggedIn: state => {
      let isLoggedIn = state.isLoggedIn;
      if (!isLoggedIn) {
        isLoggedIn = JSON.parse(window.sessionStorage.getItem("isLoggedIn"));
      }
      return isLoggedIn;
    },
    isCollected: state => {
      let isCollected = state.isCollected;
      if (!isCollected) {
        isCollected = JSON.parse(window.sessionStorage.getItem("isCollected"));
      }
      return isCollected;
    }
  },
  mutations: {
    setActiveName: (state, activeName) => {
      state.activeName = activeName;
      window.sessionStorage.setItem("activeName", JSON.stringify(activeName));
    },
    setPlayList: (state, showPlayList) => {
      state.showPlayList = showPlayList;
      window.sessionStorage.setItem(
        "showPlayList",
        JSON.stringify(showPlayList)
      );
    },
    setIsLoggedIn: (state, isLoggedIn) => {
      state.isLoggedIn = isLoggedIn;
      window.sessionStorage.setItem("isLoggedIn", JSON.stringify(isLoggedIn));
    },
    setIsCollected: (state, isCollected) => {
      state.isCollected = isCollected;
      window.sessionStorage.setItem("isCollected", JSON.stringify(isCollected));
    }
  }
};

export default configure;



