<template>
  <div class="upload">
    <h2 class="title">修改头像</h2>
    <hr />
    <div class="section">
      <el-upload
        drag
        :action="uploadAvator()"
        :on-success="handleAvatorSuccess"
        :before-upload="beforeAvatorUpload"
      >
        <i class="el-icon-upload"></i>
        <div>
          将文件拖到此处，或<span style="color: rgb(0, 157, 255);"
            >修改头像</span
          >
        </div>
        <br />
        <div slot="tip" class="el-upload__tip">
          上传文件jpg/png格式，大小不超过10M
        </div>
      </el-upload>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { mixin } from "../../mixins";
export default {
  name: "avator-setting",
  mixins: [mixin],
  computed: {
    ...mapGetters(["userId"])
  },
  methods: {
    // 上传地址
    uploadAvator() {
      return `${this.$store.state.configure.HOST}/consumer/updateConsumerPic?id=${this.userId}`;
    },
    // 上传成功
    handleAvatorSuccess(res, file) {
      if (res.code == 1) {
        this.$store.commit("setAvator", res.avator);
        this.notify("修改成功", "success");
      } else {
        this.notify("修改失败", "error");
      }
    },
    // 上传前的校验
    beforeAvatorUpload(file) {
      const isJPG = file.type == "image/jpeg";
      const isLt10M = file.size / 1024 / 1024 < 10;
      if (!isJPG) {
        this.notify("上传图片只能是jpg格式", "error");
        return false;
      }
      if (!isLt10M) {
        this.notify("图片大小超过10m", "error");
        return false;
      }
      return true;
    }
  }
};
</script>
<style scoped lang="scss">
@import "../../assets/css/upload.scss";
</style>



