<template>
  <transition name="slide-fade">
    <div v-if="showPlayList" class="the-aside">
      <h2 class="title">播放列表</h2>
      <ul class="menus">
        <li
          v-for="(item, index) in listOfSongs"
          :key="index"
          :class="{ 'is-play': id == item.id }"
          @click="
            playSong(
              item.id,
              item.url,
              item.pic,
              item.index,
              item.name,
              item.lyric
            )
          "
        >
          {{ item.name }}
        </li>
      </ul>
    </div></transition
  >
</template>
<script>
import { mapGetters } from "vuex";
import { mixin } from "../mixins";
export default {
  name: "play-list",
  mixins: [mixin],
  computed: {
    ...mapGetters([
      "showPlayList", //是否显示播放列表
      "listOfSongs", //根据歌手名模糊查询得到的歌曲列表
      "id" //当前播放歌曲的id
    ])
  },
  mounted() {
    document.addEventListener(
      "click",
      () => {
        this.$store.commit("setPlayList", false);
      },
      true
    );
  },
  methods: {
    playSong(id, url, pic, index, name, lyric) {
      this.toPlay(id, url, pic, index, name, this.parseLyric(lyric));
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/the-aside.scss";
</style>



