<template>
  <div class="the-header">
    <div class="header-logo" @click="goHome">
      <svg class="icon">
        <use xlink:href="#icon-erji"></use>
      </svg>
      <span>Music</span>
    </div>
    <ul class="navbar">
      <li
        :class="{ active: item.name == activeName }"
        v-for="item in navMsg"
        :key="item.path"
        @click="goPage(item.path, item.name)"
      >
        {{ item.name }}
      </li>
      <li>
        <div class="header-search">
          <input
            type="text"
            placeholder="搜索歌曲"
            @keyup.enter="goSearch()"
            v-model="keyWords"
          />
          <div class="search-btn" @click="goSearch()">
            <svg class="icon"><use xlink:href="#icon-sousuo"></use></svg>
          </div>
        </div>
      </li>
      <!-- 右侧导航栏登录注册 -->
      <ul v-show="!isLoggedIn">
        <li
          :class="{ active: item.name == activeName }"
          v-for="item in loginNavMsg"
          :key="item.path"
          @click="goPage(item.path, item.name)"
        >
          {{ item.name }}
        </li>
      </ul>
    </ul>
    <div class="header-right" v-show="isLoggedIn">
      <div id="user"><img :src="attachImageUrl(avator)" /></div>
      <ul class="menu">
        <li
          v-for="(item, index) in menuList"
          :key="index"
          @click="goMenuList(item.path)"
        >
          {{ item.name }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { navMsg, loginNavMsg, menuList } from "../assets/data/header";
import { mixin } from "../mixins";

export default {
  name: "the-header",
  mixins: [mixin],
  data() {
    return {
      navMsg: [], //导航栏
      loginNavMsg: [], //导航栏右侧登录注册
      keyWords: [], //搜索关键字
      menuList: [] //用户下拉菜单
    };
  },
  computed: {
    ...mapGetters(["activeName", "isLoggedIn", "avator"])
  },
  created() {
    this.navMsg = navMsg;
    this.loginNavMsg = loginNavMsg;
    this.menuList = menuList;
  },
  methods: {
    goHome() {
      this.$router.push({ path: "/" });
    },
    goPage(path, name) {
      if (!this.isLoggedIn && path == "/my-music") {
        this.notify("请先登录", "warning");
        this.$router.push({ path: "/login" });
      } else {
        this.$store.commit("setActiveName", name);
        this.$router.push({ path: path });
      }
    },
    goSearch() {
      this.$router.push({
        path: "/search",
        query: { keyWords: this.keyWords }
      });
    },
    goMenuList(path) {
      if (path === 0) {
        this.$store.commit("setIsLoggedIn", false);
        this.$router.go(0);
      } else this.$router.push({ path });
    }
  },
  mounted() {
    // 点击头像显示下拉菜单
    document.querySelector("#user").addEventListener(
      "click",
      e => {
        document.querySelector(".menu").classList.add("show");
        e.stopPropagation();
      },
      false
    );
    // 点击菜单内部选项时菜单不会关闭
    document.querySelector(".menu").addEventListener(
      "click",
      e => {
        e.stopPropagation();
      },
      false
    );
    // 点击空白处关闭下拉菜单
    document.addEventListener("click", () =>
      document.querySelector(".menu").classList.remove("show")
    );
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/the-header.scss";
</style>



