<template>
  <div class="song-lyric">
    <h1 class="lyric-title">歌词</h1>
    <!-- 有歌词 -->
    <ul class="has-lyric" v-if="lyrics.length" key="index">
      <li v-for="(item, index) in lyrics" :key="index">{{ item[1] }}</li>
    </ul>
    <!-- 没有歌词 -->
    <div v-else class="no-lyric" key="no-lyric"><span>暂无歌词</span></div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { mixin } from "../mixins";
export default {
  mixins: [mixin],
  data() {
    return {
      lyrics: [] //当前歌曲的歌词
    };
  },
  computed: {
    ...mapGetters([
      "curTime", //当前歌曲播放到的位置
      "id", //当前播放的歌曲id
      "listIndex", //当前歌曲在歌单中的位置
      "listOfSongs", //根据歌手名模糊查询得到的歌曲列表(当前播放列表)
      "lyric" //歌词
    ])
  },
  created() {
    this.lyrics = this.lyric;
  },
  watch: {
    id() {
      this.lyrics = this.parsecLyric(this.listOfSongs[this.listIndex].lyric);
    },
    curTime() {
      if (this.lyric.length > 0) {
        for (let i = 0; i < this.lyrics.length; i++) {
          if (this.curTime > this.lyrics[i][0]) {
            for (let j = 0; j < this.lyrics.length; j++) {
              document.querySelectorAll(".has-lyric li")[j].style.color =
                "#000";
              document.querySelectorAll(".has-lyric li")[j].style.fontSize =
                "15px";
            }
            if (i > 0) {
              document.querySelectorAll(".has-lyric li")[i].style.color =
                "#95d2f6";
              document.querySelectorAll(".has-lyric li")[i].style.fontSize =
                "25px";
            }
          }
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/lyric.scss";
</style>



